import React, { lazy, Suspense, useEffect } from 'react';
import './App.css';
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import * as appActions from './redux/actions/app'
import { Switch, withRouter, Route } from 'react-router-dom'
import Header from './component/Header';
import Contact from './component/Footer';
import UpSite from './component/upsite/UpSite';
import * as userActions from './redux/actions/user'
import { getOther, getSecure } from './redux/actions/app'
import { YMInitializer } from 'react-yandex-metrika';
import { getParams } from './redux/constants/other'

export const mainWindow = React.createRef();
export const scrollToTop = () => {
    window.scrollTo(0, 0);
}


const Main = lazy(() => import('./page/main'));
const Kategory = lazy(() => import('./page/kategory'));
const Signin = lazy(() => import('./page/signin'));
const Resetpass = lazy(() => import('./page/resetpass'));
const Signup = lazy(() => import('./page/signup'));
const Profile = lazy(() => import('./page/profile'));
const Item = lazy(() => import('./page/item'));
const Blogs = lazy(() => import('./page/blogs'));
const Preorders = lazy(() => import('./page/preorders'));
const Preorder = lazy(() => import('./page/preorder'));
const Mypreorders = lazy(() => import('./page/mypreorders'));
const Search = lazy(() => import('./page/search'));
const Skidki = lazy(() => import('./page/skidki'));
const Blog = lazy(() => import('./page/blog'));
const Favorite = lazy(() => import('./page/favorite'));
const Cart = lazy(() => import('./page/cart'));
const Myorders = lazy(() => import('./page/myorders'));
const Order = lazy(() => import('./page/order'));
const About = lazy(() => import('./page/about'));
const Contact1 = lazy(() => import('./page/contact'));
const Delivery = lazy(() => import('./page/delivery'));
const Faq = lazy(() => import('./page/faq'));
const Size = lazy(() => import('./page/size'));
const Uslovia = lazy(() => import('./page/uslovia'));

const App = React.memo(
    (props) =>{
        const { checkAuthenticated } = props.userActions;
        const { setData } = props.appActions;
        let params = getParams();
        useEffect(async ()=>{
            checkAuthenticated();
            let data = await getOther({name: 'Валюта'})
            if(data != undefined)
                await setData({name: 'Курс', data: data})
            data = await getOther({name: 'Цвет'})
            if(data != undefined)
                await setData({name: 'Цвет', data: data})
            data = await getOther({name: 'Разделы'})
            if(data != undefined)
                await setData({name: 'Разделы', data: data})
            data = await getSecure({name: 'Уровень'})
            if(data != undefined)
                await setData({name: 'Уровень', data: data})
            if(params.ref!=undefined){
                await setData({name: 'REF', data: params.ref});
                await getOther({name: 'Рефералка', data: {refer: params.ref}})
            }
        },[])
        return (
            <div ref={mainWindow} className='App'>
                <Header history={props.history}/>
                <Switch>
                    <Route  path='/kategory' component={WaitingComponent(Kategory, props.history, props.location)}/>
                    <Route  path='/signin' component={WaitingComponent(Signin, props.history, props.location)}/>
                    <Route  path='/resetpass' component={WaitingComponent(Resetpass, props.history, props.location)}/>
                    <Route  path='/signup' component={WaitingComponent(Signup, props.history, props.location)}/>
                    <Route  path='/profile' component={WaitingComponent(Profile, props.history, props.location)}/>
                    <Route  path='/item' component={WaitingComponent(Item, props.history, props.location)}/>
                    <Route  path='/blogs' component={WaitingComponent(Blogs, props.history, props.location)}/>
                    <Route  path='/preorders' component={WaitingComponent(Preorders, props.history, props.location)}/>
                    <Route  path='/preorder' component={WaitingComponent(Preorder, props.history, props.location)}/>
                    <Route  path='/mypreorders' component={WaitingComponent(Mypreorders, props.history, props.location)}/>
                    <Route  path='/search' component={WaitingComponent(Search, props.history, props.location)}/>
                    <Route  path='/skidki' component={WaitingComponent(Skidki, props.history, props.location)}/>
                    <Route  path='/blog' component={WaitingComponent(Blog, props.history, props.location)}/>
                    <Route  path='/favorite' component={WaitingComponent(Favorite, props.history, props.location)}/>
                    <Route  path='/cart' component={WaitingComponent(Cart, props.history, props.location)}/>
                    <Route  path='/myorders' component={WaitingComponent(Myorders, props.history, props.location)}/>
                    <Route  path='/order' component={WaitingComponent(Order, props.history, props.location)}/>
                    <Route  path='/about' component={WaitingComponent(About, props.history, props.location)}/>
                    <Route  path='/contact' component={WaitingComponent(Contact1, props.history, props.location)}/>
                    <Route  path='/delivery' component={WaitingComponent(Delivery, props.history, props.location)}/>
                    <Route  path='/faq' component={WaitingComponent(Faq, props.history, props.location)}/>
                    <Route  path='/size' component={WaitingComponent(Size, props.history, props.location)}/>
                    <Route  path='/uslovia' component={WaitingComponent(Uslovia, props.history, props.location)}/>
                    <Route  path='/' component={WaitingComponent(Main, props.history, props.location)}/>
                </Switch>
                <Contact history={props.history}/>
                <UpSite/>
                <YMInitializer accounts={[53387752]} options={{clickmap:true,
                    trackLinks:true,
                    accurateTrackBounce:true,
                    webvisor:true}} version='2' />
            </div>
        );
    })

function WaitingComponent(Component, history, location) {
    return props => (
        <Suspense fallback={<div></div>}>
            <Component {...props}  history={history} location={location}/>
        </Suspense>
    );
}

function mapStateToProps (state) {
    return {
        user: state.user,
        app: state.app
    }
}

function mapDispatchToProps(dispatch) {
    return {
        userActions: bindActionCreators(userActions, dispatch),
        appActions: bindActionCreators(appActions, dispatch),
    }
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
