import { SET_APP_STATUS } from '../constants/app'

const initialState = {
    ref: '*',
    color: {},
    lvl: 0,
    razdely: {discount: '', preorder: ''},
    currency: localStorage.currencyMissPolin===undefined?'USD':localStorage.currencyMissPolin,
    currencyCourse: {RUB: 1, KGS: 1},
}

export default function app(state = initialState, action) {
    let status;
    switch (action.type) {
        case SET_APP_STATUS:
            if (action.payload.name=='Валюта'){
                status = {...state, currency: action.payload.data}
                localStorage.currencyMissPolin = action.payload.data;
            } else if (action.payload.name=='REF'){
                status = {...state, ref: action.payload.data}
            } else if (action.payload.name=='Курс'){
                status = {...state, currencyCourse: action.payload.data}
            } else if (action.payload.name=='Цвет'){
                status = {...state, color: action.payload.data}
            } else if (action.payload.name=='Разделы'){
                status = {...state, razdely: action.payload.data}
            } else if (action.payload.name=='Уровень'){
                status = {...state, lvl: action.payload.data}
            }
            return status
        default:
            return state
    }
}